<template>
  <div class="comfirm">
      <!-- 地址 -->
      <div class="address paddi clearfix">
          <div class="left fl">
              <p class="two_t_h">王尼玛</p>
              <p class="three_t_tip company">公司</p>
          </div>
          <div class="right fr two_t_m">
              >
          </div>
          <div class="center fr">
              <p class="three_t_h">186****6666</p>
              <p class="three_t_m add">
                  广东省深圳市南山区  西丽街道中山园路 TCL 国际 E 城 F1 栋 A 座 4 层
              </p>
          </div>
      </div>
      <!-- 订单详情 -->
      <div class="detail paddi">
          <ul>
              <li class="rest clearfix">
                  <span class="iconfont isChecked fl" @click="useYuE=!useYuE" :class="[useYuE?'icon-gouxuan':'icon-weixuanzhong']"></span>
                  <span class="two_t_h fl">优先使用账户余额</span>
                  <span class="fr">¥10</span>
              </li>
          </ul>
      </div>
      <!-- 配送时间 -->
      <div class="pstime paddi">
        <ul>
          <li>
            <p class="clearfix">
              <span class="iconfont fl isChecked" @click="useShunFeng=false" :class="[useShunFeng?'icon-weixuanzhong':'icon-gouxuan']"></span>
              <span class="fl two_t_h jishi" @click="showmyPicker()">即时送（仅限同城）</span>
              <span class="fr two_t_m" @click="showmyPicker()">></span>
            </p>
            <p class="three_t_m tody" @click="showmyPicker()">{{timevalue[0]}}；{{timevalue[1]}}</p>
          </li>
          <li class="clearfix">
            <span class="iconfont fl isChecked" @click="useShunFeng=true;showPicker=false" :class="[useShunFeng?'icon-gouxuan':'icon-weixuanzhong']"></span>
              <span class="fl two_t_h">顺丰速运</span>
          </li>
        </ul>
      </div>
      <!-- 价格详情 -->
      <div class="pricedetail paddi">
        <ul>
          <li class="two_t_h clearfix">
            <span class="fl">价格总计</span>
            <span class="fr">¥57.00</span>
          </li>
          <li class="two_t_h clearfix">
            <span class="fl">配送费</span>
            <span class="fr">¥0.00</span>
          </li>
          <li class="two_t_h clearfix">
            <span class="fl">账户余额抵扣</span>
            <span v-show="useYuE" class="fr">-¥10.00</span>
            <span v-show="!useYuE" class="fr">-¥0.00</span>
          </li>
          <li class="clearfix">
            <span class="iconfont fl isChecked" @click="useFaPiao=!useFaPiao" :class="[useFaPiao?'icon-gouxuan':'icon-weixuanzhong']"></span>
            <span class="fl two_t_h">发票</span>
            <span class="fl two_t_m danwei" @click="$router.push('/shoppingBag/invoice')">单位-深圳市沃菲数字营…</span>
            <span class="fr two_t_m" @click="$router.push('/shoppingBag/invoice')">></span>
          </li>
        </ul>
      </div>
      <!-- 订单备注 -->
      <div class="beizhu paddi clearfix" @click="$router.push('/shoppingBag/remarks')">
        <span class="fl two_t_h">订单备注</span>
        <span class="fl word two_t_m">我的酸奶一定要做成咸的…</span>
        <span class="fr two_t_m">></span>
      </div>
      <!-- 确认提交 -->
      <div class="fanal clearfix">
        <span class="fl two_t_m">共 ( 1 ) 件商品</span>
        <span class="fr submit" @click="$router.push('/shoppingBag/verification')">提交订单</span>
        <span class="fr price">¥47.00</span>
      </div>
      <popup-picker style="display:none;" :show='showPicker&&!useShunFeng' :data="timeList" v-model="timevalue" @on-hide="change"></popup-picker>
  </div>
</template>

<script>
import { PopupPicker } from "vux";

export default {
  name: "Comfirm",
  data() {
    return {
      useYuE: true,
      useShunFeng: false,
      useFaPiao: true,
      showPicker: false,
      timeList: [
        ["今天", "明天"],
        [
          "10:00-12:00",
          "12:00-14:00",
          "14:00-16:00",
          "16:00-18:00",
          "18:00-20:00",
          "20:00-22:00"
        ]
      ],
      timevalue: ["今天", "10:00-12:00"]
    };
  },
  methods: {
    change() {
      this.showPicker = false;
    },
    // 控制showpicker显示
    showmyPicker() {
      if (!this.useShunFeng) {
        this.showPicker = true;
      }
    }
  },
  components: {
    PopupPicker
  }
};
</script>

<style scoped lang="less">
.comfirm {
  font-size: 0.16rem;
  background-color: #f2f2f2;
  padding-top: 0.1rem;
  padding-bottom: 0.6rem;
  .paddi {
    padding: 0 0.27rem 0 0.28rem;
  }
  .isChecked {
    font-size: 0.22rem;
    padding-right: 0.15rem;
  }
  .address {
    background-color: #fff;
    border-bottom: 0.01rem solid #f2f2f2;
    padding-top: 0.15rem;
    padding-bottom: 0.15rem;
    .left {
      text-align: center;
      .company {
        border: 0.01rem solid #e7290f;
        border-radius: 0.03rem;
        padding: 0.015rem 0.08rem;
        margin-top: 0.06rem;
      }
    }
    .center {
      margin-right: 0.3rem;
      .add {
        width: 2rem;
      }
    }
    .right {
      padding: 0.12rem 0rem;
    }
  }
  .detail {
    background-color: #fff;
    margin: 0.1rem 0rem;
    .rest {
      height: 0.52rem;
      line-height: 0.52rem;
    }
    .time {
    }
    .invoice {
    }
  }
  .pstime {
    background-color: #fff;
    padding-top: 0.15rem;
    ul {
      li {
        padding: 0.15rem 0;
        &:nth-child(1) {
          border-bottom: 0.01rem solid #f2f2f2;
          padding-top: 0rem;
        }
        .tody {
          margin-left: 0.37rem;
        }
        .jishi {
          width: 2.7rem;
        }
      }
    }
  }
  .pricedetail {
    background-color: #fff;
    margin: 0.1rem 0;
    ul {
      li {
        border-bottom: 0.01rem solid #f2f2f2;
        padding: 0.15rem 0rem;
        &:nth-last-child(1) {
          border: none;
        }
        .danwei {
          width: 2rem;
          margin-left: 0.15rem;
        }
      }
    }
  }
  .beizhu {
    background-color: #fff;
    padding-top: 0.15rem;
    padding-bottom: 0.15rem;
    .word {
      width: 2rem;
      margin-left: 0.15rem;
    }
  }

  .fanal {
    background-color: #fff;
    height: 0.5rem;
    position: fixed;
    width: 100vw;
    bottom: 0rem;
    left: 0rem;
    padding-left: 0.28rem;
    border-top: 0.01rem solid #f2f2f2;
    span {
      padding: 0.14rem 0rem;
    }
    .price {
      font-family: PingFangSC-Regular;
      font-size: 0.16rem;
      color: #e7290f;
      letter-spacing: 0;
    }
    .submit {
      padding: 0 0.18rem;
      height: 0.5rem;
      background-color: #e7290f;
      font-family: PingFangSC-Regular;
      font-size: 0.16rem;
      color: #ffffff;
      letter-spacing: 0;
      line-height: 0.5rem;
      margin-left: 0.2rem;
    }
  }
}
</style>



