
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
name: "Comfirm",
data() {
return {
useYuE: true,
useShunFeng: false,
useFaPiao: true,
showPicker: false,
timeList: [
["今天", "明天"],
[
"10:00-12:00",
"12:00-14:00",
"14:00-16:00",
"16:00-18:00",
"18:00-20:00",
"20:00-22:00"
]
],
timevalue: ["今天", "10:00-12:00"]
};
},
methods: {
change() {
this.showPicker = false;
},

showmyPicker() {
if (!this.useShunFeng) {
this.showPicker = true;
}
}
},
components: {
PopupPicker
}
};



